<template>
  <div class="content" v-if="isupdate">
    <!-- 添加 -->
    <NoticeModal :type="'add'" :visible="showModal" @closeDialog='mycloseDialog'></NoticeModal>
    <!-- 修改 -->
    <NoticeModal :type="'update'" :visible="showModal" :inputVal="changeUpdate" @closeDialog='mycloseDialog' @updatemyData='updateData'></NoticeModal>
    
    <!-- 搜索 -->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item>
        <el-input v-model="searchVal" placeholder="请输入公告标题"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSearch">搜索</el-button>
        <el-button type="primary" @click="reset">重置</el-button>
      </el-form-item>
      <el-form-item>
        <el-button class="addUser" type="primary" @click="showModal = true">添加公告</el-button>
      </el-form-item>
    </el-form>

    <!-- 表格展示 -->
    <el-table :data="myCurrentData" border style="width: 100%;margin:3rem auto;">
      <el-table-column align="center" fixed prop="id" label="公告编号">
      </el-table-column>
      <el-table-column align="center" prop="title" label="公告标题">
      </el-table-column>
      <el-table-column align="center" prop="content" label="公告内容">
      </el-table-column>
       <el-table-column align="center" prop="mytime" label="发布时间">
      </el-table-column>
      <!-- <el-table-column align="center" label="发布时间">
        <template slot-scope="scope">
          <p>{{scope.row.time.split('T')[0]}}</p>
        </template>
      </el-table-column> -->
      <el-table-column align="center" prop="name" label="发布人">
      </el-table-column>
      <el-table-column align="center" label="操作" width="250px">
        <template slot-scope="scope">
          <el-button @click="delQueding(scope.row)" type="text" size="small">删除</el-button>
          <el-button @click="updateForm(scope.row)" type="text" size="small">修改</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- pageCount 总页码  current-change 改变当前页 -->
    <el-pagination center layout="prev, pager, next" :current-page="currentPage" :page-count="myPageCount" @current-change="myhandleCurrentChange">
    </el-pagination>

  </div>
</template>

<script>
import tableMixins from '../mixins/tableMixin'
import NoticeModal from '../components/modal/NoticeModal'
export default {
    name: '',
    components: {
        NoticeModal
    },
    mixins: [tableMixins],
    data() {
        return {
            selectUrl: '/suShe/chaXunAll',
            myTableData:[],//表格总数据
            currentPage: 1,/* 当前页 */
            pageNum: 5,/* 每页显示数据条数 */
            showModal:false,//是否显示弹框
            changeUpdate:'',//点击修改时的那一条数据
            isupdate:true,
            searchVal:'',//搜索框内容
        }
    },
    computed:{
        myCurrentData() {
        // 　　console.log('currentData重新执行',this.myTableData);
            let qishi = (this.currentPage - 1) * this.pageNum
            let jieshu = qishi + this.pageNum
            return this.myTableData.slice(qishi, jieshu)
        },
        /* 总页码  一共多少页 */
        myPageCount() {
            // console.log('重新渲染页数');
            return Math.ceil(this.myTableData.length / this.pageNum)
        }
    },
    created() {
        this.onSelect({
            url:'/notice/getnotice'
        },(data)=>{
            console.log('渲染时获取的data',data);
            this.myTableData = data
        })
    },
    mounted(){
        
    },
    methods: {
        myhandleCurrentChange(val) {
            // console.log('page发生改变');
            this.currentPage = val
        },
        /* 添加确定 */
        addQueding() {
            this.add({
                url: '/suShe/xinZeng',
                data: this.formAdd
            })
        },
        /* 删除按钮 */
        delQueding(row) {
            const _this = this
            _this.$confirm('确认删除该公告吗', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
                }).then(() => {
                    console.log('row.id',row.id);
                    _this.del({
                        url: '/notice/delnotice',
                        data: { id: row.id },
                        updateUrl:'/notice/getnotice',
                        callback:function(data) {
                          _this.updateData(data)
                        }
                    })
                }).catch(() => {
                _this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        /* 点击修改 */
        updateForm(item){
            console.log('item',item);
            this.changeUpdate = item
            this.showModal = true
        },
        /* 关闭弹框 */
        mycloseDialog(){
            // console.log('传过来的取消弹框');
            this.showModal = false
            this.changeUpdate = ''
            // this.$forceUpdate();
            // this.isupdate = false
            // this.$nextTick(() => {
            //     this.isupdate = true
            // })
        },
        /* 更新data数据 */
        updateData(data){
            // console.log('我的data',data);
            this.myTableData = data
            this.currentPage = 1
        },
        /* 搜索 */
        onSearch(){
            console.log(this.searchVal);
            if (this.searchVal === '') {
                this.onSelect({
                    url:'/notice/getnotice'
                },(data)=>{
                    this.myTableData = data
                })
            } else {
                this.onSelect({
                    url:'/notice/getsinglenotice',
                    params:{
                        title:this.searchVal
                    }
                },(data)=>{
                    this.myTableData = data
                })
            }
            this.currentPage = 1
        },
        /* 重置 */
        reset(){
            this.searchVal = ''
            this.onSelect({
                url:'/notice/getnotice'
            },(data)=>{
                console.log('渲染时获取的data',data);
                this.myTableData = data
            })
        },
        /* 催款按钮 */
        dept(row) {
            const _this = this
            let type = ''
            if (row.type == 1) {
                type = 2
            } else {
                type = 1
            }
            _this.$confirm('确认催款该宿舍吗', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                _this.del({
                    url: '/dorm/deptdorm',
                    data: { 
                        id: row.id,
                        type:type
                    },
                    updateUrl:'/dorm/getdorm',
                    callback:function(data) {
                        _this.updateData(data)
                    }
                })
            }).catch(() => {
                _this.$message({
                    type: 'info',
                    message: '已取消催款'
                });          
            });
        },
    }
}
</script>

<style scoped lang="less">
.addUser {
  background-color: #fff;
  color: #07c4a8;
  border-radius: 4px;
  border: 1px solid #07c4a8;
}
.content {
  margin-top: 2rem;
}
.el-pagination {
  text-align: center;
}
</style>
