<template>
    <el-dialog v-dialogDrag center :title="isUpdate === true ? '修改公告' : '添加公告'" :visible.sync="visible" width='30%' @close='delDialog'>
        <el-form :model="formValue" label-width="80px" size='small' :rules="rules" ref="formValue">
            <el-form-item label="公告编号" prop="id" v-if="isUpdate">
                <el-input v-model.number="formValue.id" :disabled="isUpdate" placeholder="仅支持数值类型"></el-input>
            </el-form-item>
            <el-form-item label="公告标题" prop="title">
                <el-input v-model="formValue.title"></el-input>
            </el-form-item>
            <el-form-item label="公告内容" prop="content">
                <el-input type="textarea" v-model="formValue.content" ></el-input>
            </el-form-item>
            <el-form-item label="发布时间" prop="time" v-if="isUpdate">
                <el-input v-model="formValue.time" :disabled="isUpdate"></el-input>
            </el-form-item>
            <el-form-item label="发布人" prop="name" v-if="isUpdate">
                <el-input v-model="formValue.name" :disabled="isUpdate"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="delDialog">取 消</el-button>
            <el-button type="primary" @click="confirm('formValue')">确 定</el-button>
        </div>
        </el-dialog>
</template>
    
<script>
import tableMixins from '../../mixins/tableMixin'
import { getStorage } from '../../utils/changeSessionStorage'
export default {
    name: '',
    mixins: [tableMixins],
    props:[
        'type','visible','inputVal'
    ],
    data() {
        return {
            formValue:{
                id:'',
                name:'',
                title:'',
                content:'',
                time:'',
            },//表单数据
            dormData:[],
            isUpdate:false,//是否为修改弹框
            rules: {
                title: [
                    { required: true, message: '标题不能为空', trigger: 'blur' }
                ],
                content: [
                    { required: true, message: '内容不能为空', trigger: 'blur' }
                ]
            },
            userInfo:''
        }
    },
    // computed: {
        
    // },
    created() {
        this.userInfo = getStorage('userInfo')
        this.onSelect({
            url:'/dorm/getdorm'
        },(data)=>{
            console.log('获取所有宿舍',data);
            this.dormData = data
        })
    },
    mounted(){
        // console.log('this.dormData',this.dormData)
    },
    watch:{
        inputVal(){
            console.log('inputVal发生了变化',this.inputVal);
            this.formValue.id = this.inputVal.id
            this.formValue.name = this.inputVal.name
            this.formValue.title = this.inputVal.title
            this.formValue.content = this.inputVal.content
            this.formValue.time = this.inputVal.time.split('T')[0]
            this.isUpdate = true
        },
    },
    methods: {
        /* 确定 */
        confirm(formName){
            const _this = this
            _this.$refs[formName].validate((valid) => {
                if (valid) {
                    if(_this.isUpdate){
                        //修改确定
                        _this.update({
                            url: '/notice/updatenotice',
                            data: {
                                id: _this.formValue.id, 
                                title: _this.formValue.title, 
                                content: _this.formValue.content
                            },
                            updateUrl:'/notice/getnotice',
                            callback:function(data) {
                                _this.$emit('updatemyData',data)
                                _this.delDialog()
                            }
                        })
                    }else{
                        //增加确定
                        console.log('this.content',_this.formValue.content);
                        _this.add({
                            url: '/notice/addnotice',
                            data: {
                                title: _this.formValue.title, 
                                content: _this.formValue.content,
                                userId:_this.userInfo.id
                                // time:,
                            },
                            updateUrl:'/notice/getnotice',
                            callback:function(data) {
                                // console.log('回调函数中的data',data);
                                _this.$emit('updatemyData',data)
                                _this.delDialog()
                            }
                        })
                    }
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
            
        },
 
        /* 跟新确定 */
        updateQueding(row) {
            this.updateState = false
            this.update({
                url: '/suShe/gengXinById',
                data: this.formUpdate,
                method: 'post'
            })
        },
         /* 取消弹框 */
        delDialog(){
            // console.log('取消弹框');
            this.isUpdate = false
            this.formValue.id = ''
            this.formValue.stuId = ''
            this.formValue.stuName = ''
            this.formValue.stuUserId = ''
            this.formValue.stuPas = ''
            this.formValue.dormId = ''
            this.$emit('closeDialog')
        },
    
    },
   
}
</script>
